.cartDetail {
  display: flex;
}
.cartDetail .d-flex {
  display: flex;
  flex-wrap: wrap;
}
.cartDetail .line {
  border-top: 8px solid var(--primary-d1);
  width: 20%;
  margin: auto;
}
.cartDetail .cart-title {
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-size-48)/var(--unnamed-line-spacing-45) Source Serif Pro;
  letter-spacing: var(--unnamed-character-spacing-0);
  color: var(--unnamed-color-172026);
  text-align: center;
  opacity: 1;
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0.3em;
          margin-block-end: 0.3em;
}
.cartDetail .card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  width: 100%;
  margin: 10px 0px;
  margin-left: auto;
}
.cartDetail .card .img-wrapper {
  display: flex;
  margin: auto;
  max-width: 100%;
  width: 166px;
  height: 199px;
  transform: scale(1);
  vertical-align: middle;
  border-top-left-radius: calc(0.25rem - 1px);
  border-top-right-radius: calc(0.25rem - 1px);
}
.cartDetail .card .card-body {
  flex: 1 1 auto;
  padding: 1rem 1rem;
}
.cartDetail .card .card-body .icon {
  margin: 0px 10px;
}
.cartDetail .card .card-body .checkoutTilte {
  font-weight: bold;
  color: var(--unnamed-color-172026);
  text-align: left;
}
.cartDetail .card .card-body table {
  border-collapse: collapse;
  width: 100%;
}
.cartDetail .card .card-body table td, .cartDetail .card .card-body table th {
  text-align: left;
  padding: 8px;
}
.cartDetail .card .card-body table td:nth-of-type(2n) {
  text-align: right;
}
.cartDetail .card .card-body table th:nth-of-type(2n) {
  text-align: right;
}
.cartDetail .card .card-body .btn {
  border: 2px solid var(--white);
  border-radius: 4px;
  padding: 19px 25px;
  text-align: center;
  display: inline-block;
  margin: 4px 2px;
}
.cartDetail .card .card-body .addtocart {
  margin: 10px 0px;
  display: flex;
}
.cartDetail .card .card-body .addtocartBtn {
  width: 62% !important;
  height: 33px;
  color: white;
  margin: auto;
  padding: 0.375rem 0.75rem;
  order: 2px solid var(--white) !important;
  border-radius: 17px !important;
  background: var(--primary-m1) 0% 0% no-repeat padding-box !important;
}
.cartDetail .card .card-body .ppBtn {
  width: 60% !important;
  margin: auto;
  padding: 0.375rem 0.75rem;
}
.cartDetail .card .card-body .padding-title {
  padding: 1rem 1rem 0rem 1rem;
}
.cartDetail .card .card-body .cart-item-name {
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  margin-bottom: 10px;
}
.cartDetail .card .card-body p {
  -webkit-margin-before: 0.5em;
          margin-block-start: 0.5em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
}
.cartDetail .card .padding-quantity-btn {
  padding: 1rem 0.1rem;
}
.cartDetail .card .quantity {
  margin: 60px 0px;
}
.cartDetail .card .quantity input {
  -webkit-appearance: none;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border: 1px solid #B5B7BC;
  border-radius: 8px;
  text-align: center;
  width: 40%;
  height: 30px;
  margin: 0px 5px;
  background-color: white;
  font-size: 16px;
  color: #43484D;
  font-weight: 300;
}
.cartDetail .card .quantity button[class*=btn] {
  width: 30px;
  height: 30px;
  border: 1px solid #B5B7BC;
  background-color: white;
  border-radius: 20px;
  cursor: pointer;
}
.cartDetail .card .quantity .minus-btn img {
  width: 20px;
}
.cartDetail .card .quantity .plus-btn img {
  margin-top: 2px;
  width: 20px;
}
.cartDetail .card .quantity button:focus,
.cartDetail .card .quantity input:focus {
  outline: 0;
}
.cartDetail .border {
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}
@media screen and (max-width: 768px) and (min-width: 320px) {
  .cartDetail .container {
    max-width: 768px;
    padding: 0px 15px 0px 15px;
  }
  .cartDetail .cart-title {
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/var(--unnamed-line-spacing-45) var(--unnamed-font-family-source-serif-pro);
  }
  .cartDetail .card {
    margin: 6px 0px;
  }
  .cartDetail .card .padding-title {
    padding: 1rem 1rem 0rem 1rem;
  }
  .cartDetail .card .padding-quantity {
    padding: 0rem 1rem 1rem 1rem;
  }
  .cartDetail .card .quantity {
    margin: 0px 0px;
  }
  .cartDetail .border {
    border: 0px solid rgba(0, 0, 0, 0.125);
  }
  .cartDetail .checkoutBody {
    padding: 0rem 0rem !important;
  }
}
@media screen and (max-width: 1024px) and (min-width: 769px) {
  .cartDetail .container {
    max-width: 1024px;
    padding: 0px 15px 0px 15px !important;
  }
}
@media screen and (max-width: 1280px) and (min-width: 1025px) {
  .cartDetail .container {
    max-width: 1280px;
  }
}
@media screen and (max-width: 1440px) and (min-width: 1281px) {
  .cartDetail .container {
    max-width: 1440px;
  }
}
@media screen and (max-width: 1920px) and (min-width: 1441px) {
  .cartDetail .container {
    max-width: 1920px;
  }
  .cartDetail .container .addtocartBtn {
    height: 47px !important;
  }
}

.collapsible {
  background-color: white;
  color: black;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: 1px solid #D1D3D6;
  text-align: left;
  outline: none;
  font-size: 15px;
}
.collapsible span {
  font-size: 14px;
  margin: 0px 10px 0px 0px;
}

.active, .collapsible:hover {
  background-color: white;
  border: 1px solid #D1D3D6;
  color: black;
}

.content {
  padding: 0 18px;
  display: none;
  overflow: hidden;
  background-color: #f1f1f1;
}/*# sourceMappingURL=cartDetail.css.map */