}

@media screen and (max-width: 768px) and (min-width: 320px) {
  .container {
    max-width: 768px;
    padding: 0px 15px 0px 15px;
  }
  .custom--phone--hide {
    display: none;
  }
  .mr-rt-32 {
    margin-right: var(--unnamed-line-spacing-17-5) !important;
  }
  .reverse {
    display: flex;
    flex-direction: column-reverse;
  }
  .text-center {
    text-align: center !important;
  }
}
