.header {
  display: flex;
  height: 80px;
}
.header .container {
  width: 100%;
  padding: 16px 150px 16px 150px;
}
.header .logo {
  width: 100px;
  margin-top: -18px;
}
.header .center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .links {
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) muli, sans-serif;
  letter-spacing: var(--unnamed-character-spacing-0);
  text-align: left;
  color: var(--unnamed-color-1B252C);
  opacity: 1;
}
.header .icon {
  width: var(--unnamed-font-size-16);
  margin-right: 8px;
}
.header .menu-icon {
  width: 18px;
  height: 12px;
}
.header .cart-icon {
  width: var(--unnamed-font-size-20);
  margin-right: 8px;
}
.header .cart-bg {
  padding: 0.5rem 1rem;
  border-radius: 25px;
  margin-left: -1rem;
  text-decoration: none;
}
.header #lblCartCount {
  font-size: 12px;
  background: #ff0000;
  color: #fff;
  padding: 0 5px;
  vertical-align: top;
  margin-left: -10px;
}
.header .badge {
  padding-left: 9px;
  padding-right: 9px;
  border-radius: 9px;
}
.header .label-warning[href],
.header .badge-warning[href] {
  background-color: #c67605;
}
.header .bump {
  -webkit-animation: bump 300ms ease-out;
          animation: bump 300ms ease-out;
}
@-webkit-keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
@media screen and (max-width: 768px) and (min-width: 320px) {
  .header {
    height: 48px;
  }
  .header .container {
    max-width: 768px;
    padding: 0px 15px 0px 15px;
  }
  .header .logo {
    width: 100px;
    margin-top: -16px;
  }
  .header .logo-center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header .cart-bg {
    padding: 0.4rem 0.4rem;
    border-radius: 25px;
    margin-left: -0.9rem;
  }
}
@media screen and (max-width: 1024px) and (min-width: 769px) {
  .header .container {
    max-width: 1024px;
    padding: 0px 15px 0px 15px;
  }
  .header .logo {
    width: 128px;
    margin-top: -8px;
  }
  .header .logo-center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 1280px) and (min-width: 1025px) {
