@import "./../variables";
.productDetails {
    display: flex;
    .d-flex {
        display: flex;
        flex-wrap: wrap;
    }
    .row{
        margin: 0px 100px;
    }
    .img-wrapper{
        width: 50%;
    }
    .breadcrumb {
        padding: 0px 0px;
        list-style: none;
        padding-inline-start: 0px;
        margin-block-start: 0em;
        margin-block-end: 0em;
        li {
            display: inline;
            font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
                var(--unnamed-line-spacing-24) muli, sans-serif;
            letter-spacing: var(--unnamed-character-spacing-0);
            text-align: left;
            color: var(--unnamed-color-545d63);
            a {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal)
                    var(--unnamed-font-size-16) / var(--unnamed-line-spacing-24) muli, sans-serif;
                letter-spacing: var(--unnamed-character-spacing-0);
                text-align: left;
                color: var(--unnamed-color-172026);
                opacity: 1;
            }
        }
        li + li:before {
            padding: 3px;
            color: var(--unnamed-color-172026);
            content: "/\00a0";
        }
    }
    .card {
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        width: 100%;
        margin: 10px 0px;       
        a{
            text-decoration: none;
        }
        .card-body {
            flex: 1 1 auto;
            .img-wrapper {
                display: flex;
                margin: auto;
                max-width: 100%;
                width: 319px;
                height: 434px;
                transform: scale(1);
                vertical-align: middle;
                border-top-left-radius: calc(0.25rem - 1px);
                border-top-right-radius: calc(0.25rem - 1px);
            }
            .title {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) 18px/20px
                    muli, sans-serif;
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--unnamed-color-172026);
                text-align: left;
                opacity: 1;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
            .price {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
                    var(--unnamed-line-spacing-20) muli, sans-serif;
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--unnamed-color-172026);
                text-align: left;
                opacity: 1;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
            .icon {
                width: var(--unnamed-font-size-16);
                margin-right: 8px;
            }
        }
        
    }
    .card-detail {
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        background-clip: border-box;
        width: 90%;
        margin: 10px 0px;
        margin-left: auto;
        .card-header {
            padding: 0.5rem 1rem;
            margin-bottom: 0;
            background-color: rgba(0, 0, 0, 0.03);
            border-bottom: 1px solid rgba(0, 0, 0, 0.125);
        }
        a{
            text-decoration: none;
            color: #172026;
        }
        .card-body-detail {
            flex: 1 1 auto;
            padding: 1rem 1rem;
            .img-wrapper {
                display: flex;
                margin: auto;
                max-width: 100%;
                width: 319px;
                height: 380px;
                transform: scale(0.75);
                vertical-align: middle;
                border-top-left-radius: calc(0.25rem - 1px);
                border-top-right-radius: calc(0.25rem - 1px);
            }
            .title {
                font-size: 36px;
                font-weight: bold;
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--unnamed-color-172026);
                text-align: left;
                opacity: 1;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
            .price {
                font-size: 20px;
                font-weight: bold;
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--unnamed-color-172026);
                text-align: left;
                opacity: 1;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
            .icon {
                width: var(--unnamed-font-size-16);
                margin-right: 8px;
            }
            .breadcrumb, .title, .price, .rating, .description, .sizes {
                margin-bottom: 10px; }
            .divider {
                border-top: 1px solid #e4e0e0;
            }
            .btn {
                border: 2px solid var(--white);
                border-radius: 4px;
                padding: 19px 25px;
                text-align: center;
                display: inline-block;
                margin: 4px 2px;
            }
            .addtocart{
                margin: 31px 0px;
            }
            .addtocartBtn{
                width: 100% !important;
                margin: auto;
                padding: 0.375rem 0.75rem;
                order: 2px solid var(--white) !important ;
                border-radius: 17px !important;
                background: var(--primary-m1) 0% 0% no-repeat padding-box !important;
            }
            .btn-black {
                background: var(--unnamed-color-172026) 0% 0% no-repeat padding-box;
            }
            .btn-white {
                background: var(--white) 0% 0% no-repeat padding-box;
                border: 2px solid #DFE1E2;
            }
            .btn-darkGreen {
                background: var(--positive-1) 0% 0% no-repeat padding-box;
            }
            .btn-yellow {
                background: #fdba5e 0% 0% no-repeat padding-box;
            }
            .btn-blue {
                border: 2px solid var(--white) ;
                background: var(--primary-m1) 0% 0% no-repeat padding-box ;
                border-radius: 4px;
            }
            .size {
                border: 2px solid #545D63;
                border-radius: 6px;
                padding: 8px 10px;
                text-align: center;
                display: inline-block;
                margin: 4px 2px;
            }
            .quantity {
                margin-right: 60px;
              }
              .quantity input {
                -webkit-appearance: none;
                border: none;
                text-align: center;
                width: 32px;
                font-size: 16px;
                color: #43484D;
                font-weight: 300;
              }
               
              button[class*=btn] {
                width: 30px;
                height: 30px;
                background-color: #E1E8EE;
                border-radius: 20px;
                border: none;
                cursor: pointer;
                color: white;
              }
              .minus-btn img {
                width: 20px;
              }
              .plus-btn img {
                margin-top: 2px;
                width: 20px;
              }
               
              button:focus,
              input:focus {
                outline:0;
              }
              .fa-mt-rt{
                margin-right: 10px;
              }
        }
        .card-footer {
            padding: 0.5rem 1rem;
            background-color: rgba(0, 0, 0, 0.03);
            border-top: 1px solid rgba(0, 0, 0, 0.125);
        }
    }
    @media screen and (max-width: $Tablet) and (min-width: $Mobile) {
        .container {
            max-width: $Tablet;
            padding: 0px 15px 0px 15px !important;
        }
        .row{
            margin: 16px 0px;
        }
        .card {
            margin: auto;
            .card-body .img-wrapper {
                width: 163px;
                height: 196px;
            }
        }
        .card-detail {
            width: 100%;
            margin: auto;
            .cart-title {
                font: var(--unnamed-font-style-normal) normal 
                var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/var(--unnamed-line-spacing-45) 
                var(--unnamed-font-family-source-serif-pro);
            }
            .card-body-detail {
                .quantity input {
                    width: 100px;
                }
            }
        }
    }
    @media screen and (max-width: $Desktop) and (min-width: ($Tablet + 1)) {
        .card .card-body .img-wrapper {
            width: 100%;
            height: 100%;
        }
    }
    @media screen and (max-width: $Desktop-md) and (min-width: ($Desktop + 1)) {
        .card .card-body .img-wrapper {
            width: 100%;
            height: 100%;
        }
    }
    @media screen and (max-width: $Desktop-lg) and (min-width: ($Desktop-md + 1)) {
        .card .card-body .img-wrapper {
            width: 300px;
            height: 260px;
        }
    }
    @media screen and (max-width: ($Desktop-lg + 96)) and (min-width: ($Desktop-md + 1)) {
        .card{
            .card-body{
                .img-wrapper {
                    width: 100%;
                    height: 100%;

                }

            }

        }
    }
    @media screen and (max-width: $Desktop-xl) and (min-width: ($Desktop-lg + 1)) {
        .card{
                .card-body{
                    .img-wrapper {
                        width: 100%;
                        height: 100%;
                    }
                }
            }          
    }
}

.preview-thumbnail.nav-tabs {
    border: none;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
    list-style: none;   
}
.preview-thumbnail.nav-tabs li {
width: 100%;
margin-right: 2.5%; }
.preview-thumbnail.nav-tabs li img {
max-width: 70%;
display: block;
margin:  10px auto;
}
.preview-thumbnail.nav-tabs li img.active {
    border-bottom: 4px solid blue;
}


.preview-thumbnail.nav-tabs li a {
padding: 0;
margin: 0; }

.preview-thumbnail.nav-tabs li:last-of-type {
margin-right: 0; }


.first-child {
margin: 0px auto !important; 
}
.checked, .price span {
    color: #ff9f1a; }