@import "./../variables";
.card-item {
    .title{
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-20)/var(--unnamed-line-spacing-25) muli, sans-serif;
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-172026);
        text-align: left;
        margin-block-start: 0em !important;
        opacity: 1;
    }
    .price{
        // font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) muli, sans-serif;
        // letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-172026);
        font-size: 16px;
        margin-bottom: 8px;
        margin-block-start: 0em !important;
        text-align: left;
        opacity: 1;
    }
}