@import "./../variables";
.cartDetail{
    // background-color: rgb(225, 193, 193);
    display: flex;
    .d-flex {
        display: flex;
        flex-wrap: wrap;
    }
    .line {
      border-top: 8px solid var(--primary-d1);
      width: 20%;
      margin: auto;
    }
    .cart-title{
      font: var(--unnamed-font-style-normal) normal var(--unnamed-font-size-48)/var(--unnamed-line-spacing-45) Source Serif Pro;
      letter-spacing: var(--unnamed-character-spacing-0);
      color: var(--unnamed-color-172026);
      text-align: center;
      // font: normal normal 600 48px/45px Source Serif Pro;
      // letter-spacing: 0px;
      // color: #172026;
      opacity: 1;
      margin-block-start: 0em;
      margin-block-end: 0.3em;
    }
    .card {
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        // background-color: red;
        // background-clip: border-box;
        // border: 1px solid rgba(0, 0, 0, 0.125);
        // border-radius: 0.25rem;
        width: 100%;
        margin: 10px 0px;
        margin-left: auto;
        
        .img-wrapper {
            display: flex;
            margin: auto;
            max-width: 100%;
            width: 166px;
            height: 199px;
            transform: scale(1);
            vertical-align: middle;
            border-top-left-radius: calc(0.25rem - 1px);
            border-top-right-radius: calc(0.25rem - 1px);
        }
        .card-body{
            flex: 1 1 auto;
            padding: 1rem 1rem;
            .icon{
              margin: 0px 10px;
            }
            .checkoutTilte{
              font-weight: bold;
              color: var(--unnamed-color-172026);
              text-align: left;
              // font: normal normal 600 20px/25px Muli;
              // letter-spacing: 0px;
              // color: #172026;
            }
            table {
              border-collapse: collapse;
              width: 100%;
              td, th {
                // border: 1px solid #dddddd;
                text-align: left;
                padding: 8px;
              }
              td:nth-of-type(2n) {
                text-align: right;
              }
              th:nth-of-type(2n) {
                text-align: right;
              }
              
            }
            .btn {
              border: 2px solid var(--white);
              border-radius: 4px;
              padding: 19px 25px;
              text-align: center;
              display: inline-block;
              margin: 4px 2px;
          }
          .addtocart{
              margin: 10px 0px;
              display: flex;
          }
         
          .addtocartBtn{
              width: 62% !important;
              height: 33px;
              color: white;
              margin: auto;
              padding: 0.375rem 0.75rem;
              order: 2px solid var(--white) !important ;
              border-radius: 17px !important;
              background: var(--primary-m1) 0% 0% no-repeat padding-box !important;
             
          }
          .ppBtn{
            width: 60% !important;
            margin: auto;
            padding: 0.375rem 0.75rem; 
          }
          .padding-title{
            padding: 1rem 1rem 0rem 1rem;
          }
          .cart-item-name{
            font-size: 20px;
            font-weight: 400;
            line-height: 24px;
            margin-bottom: 10px;
          }
          p{
            margin-block-start: 0.5em;
            margin-block-end: 0em;
          }
          

            
        }
        .padding-quantity-btn{
          padding: 1rem 0.1rem;
        }
        .quantity {
            margin: 60px    0px;
          
           input {
            -webkit-appearance: none;
            background: #FFFFFF 0% 0% no-repeat padding-box;
            border: 1px solid #B5B7BC;
            border-radius: 8px;
            text-align: center;
            width: 40%;
            height: 30px;
            margin: 0px 5px;
            background-color: white;
            font-size: 16px;
            color: #43484D;
            font-weight: 300;
          }
           
          button[class*=btn] {
            width: 30px;
            height: 30px;
            border: 1px solid #B5B7BC;
            background-color: white;
            border-radius: 20px;
            // border: none;
            cursor: pointer;
          }
          
          .minus-btn img {
            // margin-bottom: 3px;
            width: 20px;
          }
          .plus-btn img {
            margin-top: 2px;
            width: 20px;
          }
           
          button:focus,
          input:focus {
            outline:0;
          }
          
          
          
        }
        
    }
    .border{
      background-clip: border-box;
      border: 1px solid rgba(0, 0, 0, 0.125);
      border-radius: 0.25rem;
    }

    

@media screen and (max-width: $Tablet) and (min-width: $Mobile) {
  .container {
      max-width: $Tablet;
      padding: 0px 15px 0px 15px;
  }
  .cart-title{
    font: var(--unnamed-font-style-normal) normal 
    var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/var(--unnamed-line-spacing-45)
     var(--unnamed-font-family-source-serif-pro);

  }
  .card{
    margin: 6px 0px;
  }
  .card .padding-title{
    padding: 1rem 1rem 0rem 1rem;
  }
  .card .padding-quantity{
    padding: 0rem 1rem 1rem 1rem;
  }
  
  .card .quantity {
     margin: 0px 0px; 
  }
   .border {
    border: 0px solid rgba(0, 0, 0, 0.125);
  }
  .checkoutBody {
     padding: 0rem 0rem !important; 
  }
  
}
@media screen and (max-width: $Desktop) and (min-width: ($Tablet + 1)) {
  .container {
      max-width: $Desktop;
      padding: 0px 15px 0px 15px !important;
  }
}
@media screen and (max-width: $Desktop-md) and (min-width: ($Desktop + 1)) {
  .container {
      max-width: $Desktop-md;
  }
}
@media screen and (max-width: $Desktop-lg) and (min-width: ($Desktop-md + 1)) {
  .container {
      max-width: $Desktop-lg;
  }
}
@media screen and (max-width: $Desktop-xl) and (min-width: ($Desktop-lg + 1)) {
  .container {
      max-width: $Desktop-xl;
      .addtocartBtn{
        height: 47px !important;
      }
  }
  
}


}

.collapsible {
  background-color: white;
  color: black;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: 1px solid #D1D3D6;
  text-align: left;
  outline: none;
  font-size: 15px;
  span{
    font-size: 14px;
    margin: 0px 10px 0px 0px;
  }
}

.active, .collapsible:hover {
  background-color: white;
  border: 1px solid #D1D3D6;
  color: black;
}

.content {
  padding: 0 18px;
  display: none;
  overflow: hidden;
  background-color: #f1f1f1;
}